import React, { Component } from 'react'
import ReactDOM from 'react-dom'
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'
import Image from 'react-bootstrap/Image'
import facade from '../assets/img/facade.png'
import fullscreen from '../assets/icon/fullscreen.png'
import midi from '../assets/img/piano3.jpg'
import camera from '../assets/img/camera2.jpg'
import sound from '../assets/img/audio.png'

class ExpandCard extends Component {
    constructor(props){
        super(props);
        this.onClickExpand = this.onClickExpand.bind(this);
        this.handleMouseEnter = this.handleMouseEnter.bind(this);
        this.handleMouseLeave = this.handleMouseLeave.bind(this);

        this.state = {
            modalShow: false, 
            title: 'temp',
            cardImgID: '',
            cardButtonID: '',
            cardTitleID: '',
        }
        this.handleWhichCard = this.handleWhichCard.bind(this)();
    }
    handleWhichCard(){
        if(this.props.idNum === "1"){
            this.state.title = "FAÃ‡ADE : Final Year Project";
            this.state.cardImgID = 'CardImg1';
            this.state.cardButtonID = 'CardButton1';
            this.state.cardTitleID = 'CardTitle1';
            this.state.cardImg = facade;
        }
        else if(this.props.idNum === "2"){
            this.state.title = "MIDI MUSIC PLAYING SYSTEM";
            this.state.cardImgID = 'CardImg2';
            this.state.cardButtonID = 'CardButton2';
            this.state.cardTitleID = 'CardTitle2';
            this.state.cardImg = midi;
        }
        else if(this.props.idNum === "4"){
            this.state.title = "IMAGE PROCESSING SYSTEM";
            this.state.cardImgID = 'CardImg4';
            this.state.cardButtonID = 'CardButton4';
            this.state.cardTitleID = 'CardTitle4';
            this.state.cardImg = camera;
        }
        else if(this.props.idNum === "5"){
            this.state.title = "AUDIO PROCESSING \nSYSTEM";
            this.state.cardImgID = 'CardImg5';
            this.state.cardButtonID = 'CardButton5';
            this.state.cardTitleID = 'CardTitle5';
            this.state.cardImg = sound;
        }
    }
    onClickExpand(){
        this.setState({ modalShow: true });
        if(this.props.idNum != '1'){
            //console.log(ReactDOM.findDOMNode(this.refs.fypvid).id)
        }
    }
    handleMouseEnter(){
        document.getElementById(this.state.cardImgID).style.backgroundColor = "rgb(0,0,0,0.8)";
        document.getElementById(this.state.cardTitleID).style.display = "inline";
        document.getElementById(this.state.cardButtonID).style.display = "inline";
    }
    handleMouseLeave(){
        document.getElementById(this.state.cardImgID).style.backgroundColor = "rgb(0,0,0,0)";
        document.getElementById(this.state.cardTitleID).style.display = "none";
        document.getElementById(this.state.cardButtonID).style.display = "none";

    }
    
    render(){
        let modalClose = () => this.setState({ modalShow: false });

        return(
            <div style={{position:'relative'}} onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave}>
                <div id={this.state.cardImgID} style={{position:'absolute',height:'220px' , width:'340px'}}>
                    <Image id={this.state.cardButtonID} className='CardButton' src={fullscreen}  onClick={this.onClickExpand}/>
                    <p className='CardTitle' id={this.state.cardTitleID} style={{ display:'none'}}>{this.state.title}</p>
                </div>

                <Image className="CardBox" src={this.state.cardImg} style={{height:'220px' , width:'340px'}}/>

                <MyVerticallyCenteredModal
                    idnum={this.props.idNum}
                    show={this.state.modalShow}
                    onHide={modalClose }
                    onEnter={()=>{
                        this.handleMouseLeave(); 
                        if(this.props.idNum != '1'){
                            document.getElementById('fypvid').style.display = "none";
                        }
                        else{
                            document.getElementById('fypBtn').style.display = "none";
                        }
                    }}
                />
            </div>
        );
    }
}
export default ExpandCard;

